@font-face {
  font-family: "Inter";
  src: url("../fonts/Inter-Regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@layer reset {
  header *:not(svg):not(svg *):not(strong):not(em):not(b):not(del),
  main *:not(svg):not(svg *):not(strong):not(em):not(b):not(del),
  footer *:not(svg):not(svg *):not(strong):not(em):not(b):not(del),
  .modal *:not(svg):not(svg *):not(strong):not(em):not(b):not(del) {
    all: unset;
    display: revert;
    list-style: none;
  }
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: unset;
  transition: 0.3s ease;
  cursor: pointer;
}

button {
  background-color: transparent;
  font-family: inherit;
  font-size: inherit;
  cursor: pointer;
}

img {
  display: block;
  width: auto;
  max-width: 100%;
  overflow: clip;
  overflow-clip-margin: content-box;
}

.hidden {
  display: none !important;
}

body {
  min-height: 100vh;
  position: relative;
  font-family: "Montserrat", sans-serif;
  font-style: normal;
  color: #2D2D2D;
  height: 100%;
}

html {
  height: auto;
  -webkit-text-size-adjust: none;
}
html.disable-scrolling {
  overflow: hidden;
  margin-right: calc(-1 * (100vw - 100%));
}

html,
body {
  min-height: 100vh;
  font-variant-ligatures: no-common-ligatures;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  scroll-behavior: smooth;
  scroll-padding-top: 30px;
  display: flex;
  flex-direction: column;
  margin: 0;
  position: relative;
}
html::-webkit-scrollbar,
body::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}
html::-webkit-scrollbar-track,
body::-webkit-scrollbar-track {
  background: transparent;
}
html::-webkit-scrollbar-thumb,
body::-webkit-scrollbar-thumb {
  background: #FF6600;
}

.main,
.page {
  flex: 1 1 auto;
  display: flex;
  flex-direction: column;
}

.container {
  position: relative;
  margin: 0 auto;
  width: 100%;
  padding: 0 20px;
  max-width: 1820px;
}
@media screen and (max-width: 767px) {
  .container {
    padding: 0 14px;
  }
}

.section .container {
  display: flex;
  flex-direction: column;
}
.section--main {
  padding-top: 82px;
}
.section--main .container {
  padding-top: 60px;
}
@media screen and (max-width: 767px) {
  .section--main .container {
    padding-top: 30px;
  }
}
.section--about {
  padding: 120px 0;
}
@media screen and (max-width: 767px) {
  .section--about {
    padding: 60px 0;
  }
}
.section--about .container {
  gap: 40px;
}
.section--footer {
  padding: 80px 0;
  background-color: #2D2D2D;
  border-top-right-radius: 30px;
  border-top-left-radius: 30px;
  text-align: center;
}
@media screen and (max-width: 767px) {
  .section--footer {
    padding: 40px 0;
    border-radius: 0;
  }
}
.section--footer .container {
  gap: 20px;
}
.section--grey {
  background-color: #F6F6F6;
}

.text-accent {
  color: #FF6600;
}

.styled-scrollbar::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}
.styled-scrollbar::-webkit-scrollbar-track {
  background: transparent;
}
.styled-scrollbar::-webkit-scrollbar-thumb {
  background: #FF6600;
}

.burger {
  display: none;
  z-index: 1;
  width: 50px;
  height: 50px;
  position: relative;
  cursor: pointer;
  border-radius: 10px;
  border: 2px solid #FF6600;
  transition: 0.3s ease;
}
@media screen and (max-width: 1440px) {
  .burger {
    display: block;
  }
}
@media (hover: hover) and (pointer: fine) {
  .burger:hover {
    background-color: #FF6600;
  }
  .burger:hover span {
    background-color: #FFFFFF;
  }
}
.burger span {
  position: absolute;
  left: 13px;
  width: 20px;
  height: 2.1px;
  border-radius: 2px;
  background-color: #FF6600;
  transition: transform 0.3s ease, opacity 0.3s ease, background-color 0.3s ease;
}
.burger span:nth-child(1) {
  top: 17px;
}
.burger span:nth-child(2) {
  top: 50%;
  transform: translateY(-50%);
}
.burger span:nth-child(3) {
  bottom: 17px;
}
.burger--open span:nth-child(1) {
  top: 50%;
  transform: translateY(-50%) rotate(45deg);
}
.burger--open span:nth-child(2) {
  opacity: 0;
}
.burger--open span:nth-child(3) {
  top: 50%;
  transform: translateY(-50%) rotate(-45deg);
}

