    gap: 30px;
  }
}
.banner__wrapper {
  display: flex;
  align-items: center;
  gap: 128px;
}
@media screen and (max-width: 1700px) {
  .banner__wrapper {
    gap: 60px;
  }
}
@media screen and (max-width: 1280px) {
  .banner__wrapper {
    flex-direction: column;
  }
}
@media screen and (max-width: 767px) {
  .banner__wrapper {
    gap: 30px;
  }
}
.banner__content {
  display: flex;
  flex-direction: column;
  gap: 40px;
