}
@media screen and (max-width: 1700px) {
  .banner__content {
    width: unset;
  }
}
@media screen and (max-width: 1280px) {
  .banner__content {
    width: 791px;
  }
}
@media screen and (max-width: 991px) {
  .banner__content {
    width: 100%;
  }
}
@media screen and (max-width: 767px) {
  .banner__content {
    gap: 24px;
